import React, { useRef } from 'react';
import SunEditor from 'suneditor-react';
import 'suneditor/dist/css/suneditor.min.css';
import SunEditorCore from 'suneditor/src/lib/core';
import { SunEditorOptions } from 'suneditor/src/options';
import katex from 'katex';
import { formatContent } from '../utils';
import DOMPurify from 'dompurify';

interface Props {
  value: string;
  onChange: (content: string) => void;
  placeholder?: string;
}

const options: SunEditorOptions = {
  height: '200px',
  buttonList: [
    ['undo', 'redo'],
    ['removeFormat'],
    ['bold', 'underline', 'italic'],
    ['fontColor', 'hiliteColor'],
    ['align', 'horizontalRule', 'list'],
    ['table', 'link'],
    ['fullScreen'],
  ],
  katex: katex,
  fontSize: [8, 9, 10, 11, 12, 14, 16, 18, 20],
  font: [
    'Arial',
    'Comic Sans MS',
    'Courier New',
    'Impact',
    'Georgia',
    'tahoma',
    'Trebuchet MS',
    'Verdana',
  ],
  colorList: [
    '#828282',
    '#FF5400',
    '#676464',
    '#F1F2F4',
    '#FF9B00',
    '#F00',
    '#fa6e30',
    '#000',
    'rgba(255, 153, 0, 0.1)',
    '#FF6600',
    '#0099FF',
    '#74CC6D',
    '#FF9900',
    '#CCCCCC',
  ],
};

// eslint-disable-next-line react/prop-types
const TextEditor: React.FC<Props> = ({ onChange, value, placeholder }) => {
  const editor = useRef<SunEditorCore>();

  const getSunEditorInstance = (sunEditor: SunEditorCore) => {
    editor.current = sunEditor;
  };

  const onChangeEditorValue = (html: string) => {
    html = DOMPurify.sanitize(html);
    html = formatContent(html);
    onChange(encodeURIComponent(html));
  };

  return (
    <SunEditor
      setContents={formatContent(value)}
      height="200px"
      placeholder={placeholder}
      setOptions={options}
      onChange={onChangeEditorValue}
      getSunEditorInstance={getSunEditorInstance}
    />
  );
};

export default TextEditor;
